<template>
  <div class="Register container">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-6"></div>
    </div>
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-6">
        <span class="color-main h4 font-weight-bold"></span>
        <div class="form-group text-left container py-2">
          <label for="exampleInput" class="color-main mt-2"
            ></label
          >
          <input
            type="text"
            id="exampleInput"
            class="form-control bg-input-register"
            v-model="phone"
            @change="checkphone()"
            required
          />

          <div class="text-danger" v-if="statuscheckphone === false">
            {{ errorphone }}
          </div>
          <label for="exampleInput" class="color-main mt-2"
            ></label
          >

          <input
            :type="typepassword"
            id="exampleInput"
            class="form-control bg-input-register"
            @change="checkpassword()"
            v-model="password"
            required
            onkeypress="return event.charCode >= 48 && event.charCode <= 122"
          />
          <div class="row justify-content-center">
            <div class="col-10"></div>
            <a
              class="col-2 text-center"
              style="margin-top:-30px;"
              @click="show_hide()"
            >
              <mdb-icon
                icon="eye-slash"
                class="text-dark"
                v-if="typepassword === 'password'"
              />
              <mdb-icon icon="eye" class="text-dark" v-else />
            </a>
          </div>
          <div class="text-danger" v-if="statuscheckpass === false">
            {{ errorpass }}
          </div>
          <!-- <label for="exampleInput" class="color-main mt-2"></label>
          <input type="text" id="exampleInput" class="form-control bg-input-register" />
          <div class="row justify-content-center">
            <div class="col-10"></div>
            <div class="col-2 text-center" style="margin-top:-30px;">
          <mdb-icon icon="eye-slash" class="text-dark" />-->
          <!-- <mdb-icon icon="eye" class="text-dark" /> -->
          <!-- </div>
          </div>-->
          <div>
            <label for class="color-main mt-2"></label>
            <mdb-select
              style="margin-top:-25px;"
              outline
              class="bg-input-register"
              @getValue="checkSelectbank"
              v-model="iconOptions"
              placeholder=""
            />
          </div>
          <label for class="color-main"></label>
          <input
            type="text"
            id="exampleInput"
            class="form-control bg-input-register"
            @change="checkbanknum()"
            v-model="banknumber"
          />
          <div v-if="bankCode === 'SCB'" class="col-12 px-0">
            <div class="row justify-content-center mt-2">
              <div class="col-6 col-md-6">
                <label for="validationCustom01" class="color-main"
                  ></label
                >
                <input
                  type="text"
                  class="form-control bg-input-register"
                  id="validationCustom01"
                  v-model="firstname"
                  @blur="checkfirstname()"
                  required
                  onpaste="return false"
                  oninput="this.value = this.value.toUpperCase()"
                  onkeypress="return event.charCode >= 65 && event.charCode <= 90 || event.charCode >= 97 && event.charCode <= 122"
                />
                <div class="invalid-feedback" v-if="statusfistname === false">
                  {{ errorfirstname }}
                </div>
              </div>

              <div class="col-6 col-md-6">
                <label for="validationCustom01" class="color-main"
                  ></label
                >

                <input
                  type="text"
                  class="form-control bg-input-register"
                  id="validationCustom01"
                  v-model="lastname"
                  @blur="checklastname()"
                  required
                  onpaste="return false"
                  oninput="this.value = this.value.toUpperCase()"
                  onkeypress="return event.charCode >= 65 && event.charCode <= 90 || event.charCode >= 97 && event.charCode <= 122"
                />
                <div class v-if="statuslastname === false">
                  {{ errorlastname }}
                </div>
              </div>
              <div class="col-12 col-md-12">
                <label for class="color-main">
                  <small>
                      A-Z 
                  </small>
                </label>
              </div>
            </div>
          </div>

          <div class="text-danger" v-if="statuscheckbanknum === false">
            {{ errorbaknum }}
          </div>
          <label for="exampleInput" class="color-main mt-2"></label>
          <mdb-card class="bg-input-register">
            <div class="p-2">
              <div class="row justify-content-center">
                <div class="col-3 align-self-center">
                  <mdb-switch
                    v-model="switchValue"
                    offLabel
                    onLabel
                    classes="success"
                  />
                </div>
                <div class="col-9 align-self-center">
                  <small class="text-dark">
                    <span> - </span>
                    <br />
                    <span>
                      
                      
                    </span>
                    <br />
                    <span>
                      *
                      
                    </span>
                  </small>
                </div>
              </div>
            </div>
          </mdb-card>
          <div class="row justify-content-center py-4">
            <div class="col-5 h6 align-self-center text-center">
              <router-link to="/Login" class="color-main"></router-link>
            </div>
            <div class="col-7 h5">
              <mdb-btn
                color="info"
                class="btn-block"
                rounded
                :disabled="
                  phone == null ||
                    password == null ||
                    iconOptions == null ||
                    banknumber == null
                "
                @click="register()"
                v-if="!Isloading"
              >
                <span class="h6"></span>
              </mdb-btn>
              <mdb-btn color="info" class="btn-block" rounded v-else>
                <span
                  class="spinner-grow spinner-grow-sm"
                  role="status"
                  aria-hidden="true"
                ></span>
                <span class="h6"></span>
              </mdb-btn>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
var apiGetIp = "https://api.ipify.org";
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
const jwt = require("jsonwebtoken");
import $ from "jquery";
export default {
  data() {
    return {
      switchValue: true,
      bankjson: {
        KBANK: "  ()",
        SCB: "  ()",
        KTB: "  ()",
        BAY: "  ()",
        GSB: "",
        UOB: "  ()",
        BBL: "  ()",
        TMB: "  ()",
        CI: "",
        LNH: "  ",
        TBANK: "  ()",
        BAAC: "",
        OSK: "",
        KNK: "  ()",
        JPK: "  ",
        CIMB: "   ()",
        DOIB: "",
        TISGO: "  ()",
        TCREDIT: " ",
        BNP: "  ",
        MSU: "   ",
        MEGA: " ",
        SNT: " ()",
        CN: " ()",
        USA: "",
        ASL: "",
        IZBS: " ()",
        // HK: " ",
      },
      bankType: {
        SCB: "",
        KBANK: "",
        BBL: "",
        KTB: "",
        BAY: "",
        KNK: "",
        JPK: " ",
        CI: "",
        CIMB: "",
        ZTM: " ",
        DOIB: "",
        TMB: "",
        TISGO: "",
        TCREDIT: "",
        TBANK: "",
        BNP: "",
        BAAC: "",
        MSU: "",
        MEGA: "",
        UOB: "",
        LNH: "  ",
        SNT: "",
        CN: "",
        USA: "",
        GSB: "",
        OSK: "",
        ASL: "",
        IZBS: " ",
        HK: "",
      },
      iconOptions: [],
      bankCode: null,
      phone: null,
      password: null,
      banknumber: null,
      errorphone: [],
      errorpass: [],
      errorbaknum: [],
      firstname: null,
      lastname: null,
      error: [],
      statuscheckphone: true,
      statuscheckpass: true,
      statuscheckbanknum: true,
      statusfistname: true,
      statuslastname: true,
      typepassword: "password",
      Isloading: false,
    };
  },
  computed: {
    ...mapGetters({
      version: "version",
      token: "token",
    }),
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      updateAmount: "updateAmount",
    }),
    pushbank() {
      var bl = this.bankjson;
      var n = Object.keys(bl).length;
      var nj = Object.keys(bl);
      for (var x = 0; x < n; x++) {
        this.iconOptions.push({
          text: bl[nj[x]],
          value: nj[x],
          icon: "images/bank/" + nj[x] + ".png",
        });
      }
    },
    checkSelectbank: function(value) {
      this.bankCode = value;
      // if (this.bankCode === undefined) {
      //   this.error.push("");
      // }
    },
    phoneValidator() {
      var phoneno = /^((1[3578][0-9])+\d{8})$/;

      if (this.phone.value.match(phoneno)) {
        console.log("true");
      } else {
        console.log("false");
      }
    },
    checkphone() {
      if (this.phone != null) {
        if (this.phone[0] === "0") {
          if (
            this.phone[1] === "8" ||
            this.phone[1] === "9" ||
            this.phone[1] === "6"
          ) {
            var n = this.phone.length;
            if (n === 10) {
              this.error = [];
              this.statuscheckphone = true;
            } else {
              this.statuscheckphone = false;
              this.errorphone = " 10 ";
              this.error.push(" 10 ");
            }
          } else {
            this.errorphone =
              " 06  08  09 ";
            this.statuscheckphone = false;
            this.error.push(
              " 06  08  09 "
            );
          }
        } else {
          this.errorphone = " 0 ";
          this.statuscheckphone = false;
          this.error.push(" 0 ");
        }
      } else {
        this.errorphone = "";
        this.statuscheckphone = false;
        this.error.push("");
      }
    },
    //checkbanknum
    checkbanknum() {
      if (this.banknumber !== null) {
        var bankn = this.banknumber.length;
        if (bankn > 8) {
          this.statuscheckbanknum = true;
        } else {
          this.statuscheckbanknum = false;
          this.errorbaknum = "";
          this.error.push("");
        }
      } else {
        this.statuscheckbanknum = false;
        this.errorbaknum = "";
        this.error.push("");
      }
    },
    show_hide: function() {
      if (this.typepassword === "password") {
        this.typepassword = "text";
      } else {
        this.typepassword = "password";
      }
    },
    //checkpass
    checkpassword() {
      if (this.password !== null) {
        let plenght = this.password.length;
        if (plenght < 8 || plenght > 16) {
          this.statuscheckpass = false;
          this.errorpass = " 8 - 16 ";
          this.error.push(" 8 - 16 ");
        } else {
          let msg = [false, false, false];
          for (let index = 0; index < plenght; index++) {
            if (
              this.password.charCodeAt(index) >= 48 &&
              this.password.charCodeAt(index) <= 57
            ) {
              msg[0] = true;
            } else if (
              this.password.charCodeAt(index) >= 97 &&
              this.password.charCodeAt(index) <= 122
            ) {
              msg[1] = true;
            } else if (
              this.password.charCodeAt(index) >= 65 &&
              this.password.charCodeAt(index) <= 90
            ) {
              msg[2] = true;
            } else {
              this.statuscheckpass = false;
            }
          }
          if (msg[0] && msg[1] && msg[2]) {
            this.statuscheckpass = true;
            this.error = [];
          } else {
            this.errorpass =
              "";
            this.error.push(
              ""
            );
            this.statuscheckpass = false;
          }
        }
      } else {
        this.statuscheckpass = false;
        this.errorpass = "";
        this.error.push("");
      }
    },
    checkfirstname() {
      if (this.bankCode === "SCB") {
        if (this.firstname != null) {
          this.statusfistname = true;
        } else {
          this.statusfistname = false;
          // this.statuslastname = false;
          this.errorfirstname = "";
          // this.errorlastname = "";
          this.error.push("");
        }
      } else {
        this.firstname = "";
      }
    },
    checklastname() {
      if (this.bankCode === "SCB") {
        if (this.lastname != null) {
          this.statuslastname = true;
        } else {
          this.statuslastname = false;
          // this.statuslastname = false;
          this.errorlastname = "";
          // this.errorlastname = "";
          this.error.push("");
        }
      } else {
        this.lastname = "";
      }
    },
    checkbankcode() {
      if (this.bankCode === null) {
        this.error.push("");
      }
    },
    checkValue() {
      this.checkphone();
      this.checkpassword();
      this.checkbanknum();
      this.checkfirstname();
      this.checklastname();
      this.checkbankcode();
    },
    login: function() {
      let payload = {
        phone_number: this.phone,
        password: this.password,
        ip: this.ipuser,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/login", { token: token })
        .then((response) => {
          if (response.data.msg != "USER_NOT_FOUND") {
            this.$session.set("version", this.version);
            this.$session.set("isLogin", true);
            this.$session.set("token", response.data);
            this.storeLogin(response.data);
            this.$router.push("/Otp");
          } else {
            this.errors.push("");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      0;
    },

    register() {
      this.checkValue();

      if (this.error.length === 0) {
        let payload = {
          date_regis: moment(new Date()).format("Y-MM-DD hh:mm:ss"),
          password: this.password,
          bank_type: this.bankType[this.bankCode],
          bank_code: this.bankCode,
          bank_number: this.banknumber,
          phone_number: this.phone,
          is_bonus: this.switchValue ? 1 : 0,
          join: this.join,
          active: 0,
          affiliate: "",
          fullname_bank:
            this.firstname.toUpperCase() + " " + this.lastname.toUpperCase(),
        };
        let token = jwt.sign(payload, this.$keypayload, {
          expiresIn: "300s",
        });
        this.Isloading = true;
        $(".showloader").show();
        this.$axios.post("/register", { token: token }).then((response) => {
          this.Isloading = false;
          if (response.data.code === "ER_DUP_ENTRY") {
            $(".showloader").hide();
            this.$swal({
              title: "",
              text: "  ",
              icon: "error",
              showConfirmButton: true,
            });
          } else if (response.data.code === "ERROR") {
            $(".showloader").hide();
            this.$swal({
              title: "",
              text: response.data.msg,
              icon: "error",
              showConfirmButton: true,
            });
          } else {
            // $(".preloader").hide();
            // let payload = {
            //   username: response.data.username,
            //   join: this.affiliateID,
            //   service: "AVENGER98TH"
            // };
            // let token = jwt.sign(payload, this.$keypayload, {
            //   expiresIn: "300s"
            // });
            // this.$axios.post("/affiliate", { token: token });
            this.login();
          }
        });
      }
    },
  },
  mounted() {
    this.pushbank();
    axios.get(apiGetIp).then((response) => {
      this.ipuser = "" + response.data;
    });
    this.join = this.$route.query.ref;
    if (this.join != undefined) {
      this.checkdataAff = true;
      this.dataAff = "INVITFRIEND";
      let payload = {
        join: this.join,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/AffrefDE", { token: token })
        .then((response) => {
          this.join = response.data.payload.token;
        })
        .catch(function(error) {
          return error;
        });
    } else {
      this.checkdataAff = false;
    }
  },
};
</script>

<style></style>
